
@media only screen and (orientation: landscape) {
  * {
    padding: 0;
    margin: 0;
  }
  
  html {
    background: black;
  }
  
  .frame {
    width: 100%;
    height: 20vh;
  }
  
  .parts {
    width: 100%;
    height: 20vh;
    background: #1e1e1f;
    display: flex;
    margin-top: 1px;
  }
  
  .name {
    width: 60%;
    height: 100%;
    color: white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 800;
    font-size: 5vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .extra {
    width: 20%;
    height: 100%;
  }
  
  .header {
    width: 100%;
    height: 15vh;
    box-shadow: 0px 40px 106px rgba(219, 212, 212, 0.862);
    display: flex;
    background-color: #1e1e1f;
  }
  
  .logo {
    height: 100%;
    width: 50%;
    display: flex;
    align-items: center;
  }
  
  .logo img {
    height: 60%;
    margin-left: 1vw;
    border-radius: 10vw;
  }
  
  .buttons {
    font-size: 2.5vh;
    font-family: "Share Tech", serif;
    font-weight: 800;
    margin-left: 25vw;
    width: 25vw;
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    color: white;
  }
  
  .buttons div:hover {
    transform: scale(1.05);
    text-decoration: underline;
  }
  


}
@media only screen and (orientation: portrait) {
  * {
  padding: 0;
  margin: 0;
}

html {
  background: black;
}

.frame {
  width: 100%;
  height: 20vh;
}

.parts {
  width: 100%;
  height: 20vh;
  background: #1e1e1f;
  display: flex;
  margin-top: 1px;
}

.name {
  width: 60%;
  height: 100%;
  color: white;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: 800;
  font-size: 4vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.extra {
  width: 20%;
  height: 100%;
}

.header {
  width: 100%;
  height: 15vh;
  box-shadow: 0px 40px 106px rgba(219, 212, 212, 0.862);
  display: flex;
  background-color: #1e1e1f;
}

.logo {
  height: 100%;
  width: 50%;
  display: flex;
  align-items: center;
}

.logo img {
  height: 60%;
  margin-left: 1vw;
  border-radius: 10vw;
}

.buttons {
  font-size: 2.5vh;
  font-family: "Share Tech", serif;
  font-weight: 800;
  margin-left: 25vw;
  width: 25vw;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: white;
  display: none;
}

.buttons div:hover {
  transform: scale(1.05);
  text-decoration: underline;
}

}


